<template>
    <div class="ex2">
        <div class="app-sec">
            <div class="app-col">
                <h2>Example 2: Sync With Native Scroll</h2>
                <p>Content scroll in horizontal dimension with <code>scrollLeft</code> property</p>
                <pre data-highlight>
const viewport = document.querySelector('.example2-viewport');
const content = document.querySelector('.example2-content');

new ScrollBooster({
  viewport,
  content,
  direction: 'horizontal',
  onUpdate: (state) => {
    viewport.scrollLeft = state.position.x;
  }
});</pre>
            </div>
            <div class="app-col">
                <app-table viewport-class="example2-viewport" content-class="example2-content"></app-table>
            </div>
        </div>
    </div>
</template>

<script>
    import ScrollBooster from 'scrollbooster'
    import AppTable from './Table.vue'

    export default {
        name: "app-example2",
        components: { AppTable },
        data() {
            return {}
        },
        mounted() {
            initScroll()
        }
    }

    function initScroll() {
        const viewport = document.querySelector('.example2-viewport');
        const content = document.querySelector('.example2-content');

        new ScrollBooster({
            viewport,
            content,
            direction: 'horizontal',
            onUpdate: (state) => {
                viewport.scrollLeft = state.position.x
            }
        })
    }

</script>

<style lang="less">
    .ex2 .tbl {
        background: #fff;
        &-main {
            overflow-x: scroll;
            -webkit-overflow-scrolling: touch;
        }
    }
</style>