<template>
    <div class="ex1">
        <div class="app-sec">
            <div class="app-col">
                <h2>Example 1: With Transform</h2>
                <p>Basic horizontal scroll with <code>transform</code> property</p>
                <pre data-highlight>
const viewport = document.querySelector('.example1-viewport');
const content = document.querySelector('.example1-content');

new ScrollBooster({
  viewport, // viewport container element, this parameter is required
  content, // scrollable content element
  direction: 'horizontal', // allow only horizontal scrolling
  onUpdate: (state) => {
    // your scroll logic goes here
    content.style.transform = `translateX(${-state.position.x}px)`;
  }
});</pre>
            </div>
            <div class="app-col">
                <app-table viewport-class="example1-viewport" content-class="example1-content"></app-table>
            </div>
        </div>
    </div>
</template>

<script>
    import ScrollBooster from 'scrollbooster'
    import AppTable from './Table.vue'

    export default {
        name: "app-example1",
        components: { AppTable },
        mounted() {
            initScroll()
        }
    }

    function initScroll() {
        const viewport = document.querySelector('.example1-viewport')
        const content = document.querySelector('.example1-content')

        new ScrollBooster({
            viewport,
            content,
            direction: 'horizontal',
            emulateScroll: true,
            onUpdate: (state) => {
                content.style.transform = `translateX(${-state.position.x}px)`
            }
        })
    }

</script>

